<template>
  <div class="bg">
    <div class="cont">
      <div class="aside">
        <img src="../../assets/images/loginAside.png" alt="">
      </div>
      <div class="main">
        <div class="logo">
          <img src="../../assets/images/loginLogo.png" alt="">
        </div>
        <Form ref="formVal" :model="formVal" :rules="rulesForm">
          <FormItem prop="username">
            <Input v-model="formVal.username" size="large" prefix="md-contact" placeholder="Enter your name"/>
          </FormItem>
          <FormItem prop="password">
            <Input v-model="formVal.password" size="large" prefix="md-key" placeholder="Enter your password"/>
          </FormItem>
          <FormItem>
            <Button type="primary" size="large" shape="circle" long @click="submit('formVal')">登录</Button>
          </FormItem>
        </Form>
      </div>
    </div>
  </div>
</template>

<script>
  import Menu from '../../router'

  export default {
    name: 'Login',
    data() {
      return {
        formVal: {
          username: 'liujiayi',
          password: '12345678'
        },
        rulesForm: {
          username: [
            {required: true, trigger: 'blur'}
          ],
          password: [
            {required: true, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      submit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$ajax({
              method: 'post',
              url: 'admin',
              data: this.formVal
            }).then((res) => {
              if (res.data.code == 1) {
                this.$router.push({path: '/home'})
              } else {
                this.$Notice.error({
                  title: res.data.msg,
                })
              }
            }).catch((res) => {
              this.$Notice.error({
                title: res.data.msg,
              })
            })
          } else {
            this.$Notice.error({
              title: '校验错误',
            })
          }
        })
      },
      setMenu() {
        const menu = Menu.options.routes.splice(2)
        if (menu.length > 0) {
          window.sessionStorage.setItem("SkyLarkBack", JSON.stringify(menu))
        }
      }
    },
    mounted() {
      this.setMenu()
    }
  }

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .bg {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../assets/images/loginBg.png");
  }

  .cont {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    width: 1200px;
    height: 700px;
    border-radius: 26px;
    box-shadow: 0 0 20px 20px #4057f9;
    background: #fff;
  }

  .logo {
    margin-bottom: 90px;
    width: 90%;
    height: 90%;
  }

  .logo img {
    width: 100%;
    height: 100%;
  }
</style>
